"use client";
import { appendUserLocal } from "../utils/csv";
import { saveUser } from "../utils/db";

export default function SignInButton() {
  async function handleLogin() {
    const name = prompt("Enter your name");
    const email = prompt("Enter your email");

    if (!name || !email) return;

    const payload = {
      name,
      email,
      loginAt: new Date().toISOString(),
    };

    // üîπ localStorage (quick access)
    localStorage.setItem("tp_user", JSON.stringify(payload));
    localStorage.setItem("tp_logged_in", "true");

    // üîπ CSV concept (local)
    appendUserLocal(payload);

    // üîπ IndexedDB (offline + production ready)
    await saveUser(payload);

    alert("Login successful");
  }

  return <button onClick={handleLogin}>üîê Sign In</button>;
}
